/* eslint-disable brace-style */
/* eslint-disable multiline-ternary */
import {
  Fragment,
  useContext,
  useEffect,
  useState,
  lazy,
  Suspense
} from "react"
import { useParams } from "react-router-dom"
import toast from "react-hot-toast"
import Cleave from "cleave.js/react"
import {
  Row,
  Col,
  Form,
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  CardText
} from "reactstrap"
import { useTranslation } from "react-i18next"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import "moment/locale/id"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { selectThemeColors, sortedObject } from "@utils"
import Select from "react-select"
import Flatpickr from "react-flatpickr"
import "@styles/react/libs/flatpickr/flatpickr.scss"
import { Indonesian } from "flatpickr/dist/l10n/id.js"
import { useForm, Controller } from "react-hook-form"
import {
  storeName,
  pageTitle,
  postData,
  putData,
  resetSelectedData,
  getDataById,
  dynamicForm
} from "../store"

import { useDispatch, useSelector } from "react-redux"

const ModalAddEdit = ({
  show,
  setShow,
  detailPage,
  formState,
  setFormState,
  currentData = null
}) => {
  const { t } = useTranslation()
  const { id } = useParams()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const { colors } = useContext(ThemeColors)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const allStore = useSelector((state) => state)
  const [dynamicComponents, setDynamicComponents] = useState({})
  const [formDatas, setFormDatas] = useState({})
  const [formDatasError, setFormDatasError] = useState({})

  const {
    control,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({})

  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
        <CardText className="mb-0 mt-1">{t(loadingText)}...</CardText>
      </Fragment>
    )
  }

  const countChanges = (data) => {
    if (formState === "edit") {
      const storeData = { ...sortedObject(allStore[storeName].selectedData) }
      delete storeData["id"]
      for (let key in storeData) {
        if (!(key in data)) delete storeData[key]
      }
      // console.log(JSON.stringify(sortedObject(storeData)))
      // console.log(JSON.stringify(sortedObject(data)))
      return (
        JSON.stringify(sortedObject(storeData)) !==
        JSON.stringify(sortedObject(data))
      )
    } else {
      return true
    }
  }

  const updateDataToApi = () => {
    if (countChanges(formDatas)) {
      if (
        Object.values(data).every((field) => {
          return field !== null && field !== ""
        })
      ) {
        setLoadingText(`${t("Updating")} ${t(pageTitle)} ${t("Detail")} Data`)
        dispatch(putData(data)).then((res) => {
          // console.log(res)
          if (res.meta.requestStatus === "fulfilled") {
            setShow(!show)
            toast.success(`${t("Successfully Updated")} ${t(pageTitle)}`, {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            })
            if (detailPage) {
              dispatch(getDataById(id))
            }
          } else if (res.meta.requestStatus === "rejected") {
            toast.error(res.payload.message, {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          }
        })
      } else {
        for (const key in data) {
          if (data[key]?.length === 0) {
            setError(key, {
              type: "manual"
            })
          }
        }
      }
    } else {
      toast(`${t("You haven't changed anything")}`, { icon: "ðŸ‘" })
    }
  }

  const postDataToApi = () => {
    if (
      Object.values(formDatas).every((field) => {
        return field !== null && field !== ""
      })
    ) {
      setLoadingText(`${t(`Creating a New ${pageTitle}`)}`)
      dispatch(postData(formDatas)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setShow(!show)
          toast.success(`${t("Successfully Created")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
          if (detailPage) {
            dispatch(getDataById(id))
          }
        } else if (res.meta.requestStatus === "rejected") {
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    } else {
      // console.log("ERORR")
      // console.log(formDatas)
      let errorFields = Object.assign({}, formDatasError)
      for (let key in formDatas) {
        // console.log(key, formDatas[key])
        if (formDatas[key] === null || formDatas[key]?.length === 0) {
          errorFields[key] = true
        }
      }
      // console.log(errorFields)
      setFormDatasError(errorFields)
    }
  }

  const handleReset = () => {
    dispatch(resetSelectedData())
  }

  const handleChangeInputs = (value, fieldName, isMulti = false) => {
    // console.log(value, fieldName, isMulti)
    if (isMulti) {
      if (formDatas[fieldName]) {
        const index = formDatas[fieldName].indexOf(value)
        if (index !== -1) {
          formDatas[fieldName].splice(index, 1)
        } else {
          formDatas[fieldName].push(value)
        }
      } else {
        formDatas[fieldName] = [value]
      }
    } else {
      formDatas[fieldName] = value
    }
    // formDatasError[fieldName] = false
    setFormDatasError({ ...formDatasError, [fieldName]: false })
  }

  useEffect(() => {
    // DYNAMIC IMPORT COMPONENTS
    let componentsImport = {}
    // const dynamicFormRelation = dynamicForm.filter(function (el) {
    //   return el.type === "dropdown" || el.type === "table"
    // })

    const dynamicFormRelation = dynamicForm
    for (let i = 0; i < dynamicFormRelation.length; i++) {
      const fieldName = dynamicFormRelation[i]["fieldName"]
      const fieldLabel = dynamicFormRelation[i]["fieldLabel"]
      const path_name = dynamicFormRelation[i]["fieldName"].replaceAll(
        "id_",
        ""
      )

      formDatas[fieldName] = null
      formDatasError[fieldName] = false

      ////////////INPUT - DROPDOWN
      if (
        dynamicFormRelation[i]["type"] === "dropdown" ||
        dynamicFormRelation[i]["type"] === "table"
      ) {
        const MyComponent = lazy(() => import(`../../${path_name}`))
        componentsImport[`${path_name}_comp`] = (
          <Fragment>
            <Label className="form-label" for={fieldName}>
              {t(fieldLabel ?? "")}
            </Label>
            <MyComponent
              key={`dynInput_${fieldName}`}
              lookupMode={true}
              lookupMode_fieldName={fieldName}
              lookupMode_dropdown={
                dynamicFormRelation[i]["type"] === "dropdown"
              }
              lookupMode_isMulti={
                dynamicFormRelation[i].isMultiSelection ?? false
              }
              lookupMode_onChange={handleChangeInputs}
              lookupMode_defaultValue={
                dynamicFormRelation[i]?.isM2MDefValFromGrid
                  ? allStore[storeName].data.map((x) => x[fieldName])
                  : allStore[storeName]?.selectedData
                  ? allStore[storeName].selectedData[fieldName]
                  : ""
              }
              lookupMode_isM2MDefValFromGrid={
                dynamicFormRelation[i]?.isM2MDefValFromGrid ?? false
              }
            />
          </Fragment>
        )
      }

      ////////////INPUT - TEXT
      else {
        componentsImport[`${path_name}_comp`] = (
          <Fragment>
            <Label className="form-label" for={fieldName}>
              {t(fieldLabel ?? "")}
            </Label>
            <Input
              key={`dynInput_${fieldName}`}
              defaultValue={
                dynamicFormRelation[i]?.isM2MDefValFromGrid
                  ? allStore[storeName].data.map((x) => x[fieldName])
                  : allStore[storeName]?.selectedData
                  ? allStore[storeName].selectedData[fieldName]
                  : ""
              }
              onChange={(e) =>
                handleChangeInputs(
                  e.target.value,
                  fieldName,
                  dynamicFormRelation[i].isMultiSelection ?? false
                )
              }
              // className={
              //   !formDatasError[fieldName]
              //     ? "form-control is-invalid"
              //     : "form-control"
              // }
              id={`dynInput_${fieldName}`}
            />
          </Fragment>
        )
      }
    }

    setDynamicComponents(componentsImport)
  }, [])

  return (
    <Modal
      isOpen={show}
      toggle={() => {
        handleReset()
        setShow(!show)
      }}
      className="modal-dialog-centered modal-lg"
    >
      <UILoader
        blocking={store.isLoadingAddEdit || store.isLoadingDetail}
        loader={<Loader />}
      >
        <ModalHeader
          className="bg-transparent"
          toggle={() => {
            handleReset()
            setShow(!show)
          }}
        ></ModalHeader>
        <ModalBody className="px-sm-5 pt-50 pb-5">
          <div className="text-center mb-2">
            <h2 className="mb-1">
              {formState === "edit" ? t("Edit") : t("Add Sub")} {t(pageTitle)}
            </h2>
            <p className="mb-1">
              {t("Child for ")} {currentData ? t(currentData.title) : ""}
            </p>
          </div>
          <Form
            onSubmit={
              formState === "edit"
                ? handleSubmit(updateDataToApi)
                : handleSubmit(postDataToApi)
            }
          >
            <Row className="gy-1 pt-75">
              {dynamicForm.map((item, index) => {
                const path_name = item.fieldName.replaceAll("id_", "")
                if (["dropdown", "table"].includes(item.type)) {
                  if (item.type === "table") {
                    return (
                      <Col key={path_name} md={12} xs={12}>
                        <Suspense fallback={<div>Loading...</div>}>
                          <div
                            className={
                              !formDatasError[item.fieldName]
                                ? "dynamicForm_input"
                                : "dynamicForm_input dynamicForm_input_invalid"
                            }
                          >
                            {dynamicComponents[`${path_name}_comp`]}
                            {item?.isM2MDefValFromGrid &&
                              formDatas[item.fieldName] && (
                                <div
                                  style={{ textAlign: "right" }}
                                  className="text-primary"
                                >
                                  {formDatas[item.fieldName]?.length}{" "}
                                  {t(item.fieldLabel)} {t("Selected")}
                                </div>
                              )}
                          </div>
                        </Suspense>
                      </Col>
                    )
                  } else {
                    return (
                      <Col key={path_name} md={12} xs={12}>
                        <Suspense fallback={<div>Loading...</div>}>
                          <div
                            className={
                              !formDatasError[item.fieldName]
                                ? ""
                                : "dynamicForm_input_invalid"
                            }
                          >
                            {dynamicComponents[`${path_name}_comp`]}
                            {item?.isM2MDefValFromGrid &&
                              formDatas[item.fieldName] && (
                                <div
                                  style={{ textAlign: "right" }}
                                  className="text-primary"
                                >
                                  {formDatas[item.fieldName]?.length}{" "}
                                  {t(item.fieldLabel)} {t("Selected")}
                                </div>
                              )}
                          </div>
                        </Suspense>
                      </Col>
                    )
                  }
                } else {
                  return (
                    <Col key={path_name} md={12} xs={12}>
                      <div
                        className={
                          !formDatasError[item.fieldName]
                            ? ""
                            : "dynamicForm_input_invalid"
                        }
                      >
                        {dynamicComponents[`${path_name}_comp`]}
                      </div>
                    </Col>
                  )
                }
              })}

              <Col xs={12} className="text-center mt-2 pt-50">
                <Button type="submit" className="me-1" color="primary">
                  {t("Submit")}
                </Button>
                <Button
                  type="reset"
                  color="secondary"
                  outline
                  onClick={() => {
                    handleReset()
                    setFormState("add")
                    setShow(false)
                  }}
                >
                  {t("Cancel")}
                </Button>
              </Col>
            </Row>
          </Form>
        </ModalBody>
      </UILoader>
    </Modal>
  )
}

export default ModalAddEdit
